@import url('https://fonts.googleapis.com/css2?family=Oleo+Script+Swash+Caps:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inknut+Antiqua:wght@300;400;500;600;700;800;900&display=swap');

.App {
    background-color: rgb(241, 180, 47);
    overflow: hidden;
}

#mainDiv {
    background-color: rgba(0, 0, 0, .45);
    background-image: url('./images/pizza-bg.jpg');
    height: 100vh;
    width: 100vw;
    background-size: cover;
    background-position: center;
    background-blend-mode: multiply;
}


.testingThis {
    margin-top: 30vh !important;
    background-color: rgba(0, 0, 0, .3);
    color: white;
    font-size: 2em;
    font-weight: 500 !important;
    padding: 4%;
    margin-right: 2% !important;
    font-family: 'Inknut Antiqua', serif !important;
}

